<template>
    <transition name="w-message-fade" @after-leave="handleAfterLeave">
        <div :class="[
        'w-message',
        type && !iconClass ? `w-message--${ type }` : '',
        showClose ? 'is-closable' : '',
        customClass
      ]" :style="positionStyle" v-show="visible" @mouseenter="clearTimer"
             @mouseleave="startTimer">
            <i :class="iconClass" v-if="iconClass"></i>
            <i :class="typeClass" v-else></i>
            <slot>
                <div v-html="message" class="w-message__content"></div>
            </slot>
            <i v-if="showClose" class="w-message__close el-icon-close"
               @click="close"></i>
        </div>
    </transition>
</template>

<script>
export default {
    data() {
        return {
            visible: false,
            message: '',
            duration: 3000,
            type: 'info',
            iconClass: '',
            customClass: '',
            onClose: null,
            showClose: false,
            closed: false,
            verticalOffset: 20,
            timer: null,
        }
    },
    computed: {
        typeClass() {
            return this.type && !this.iconClass ? `el-message__icon el-icon-${this.type}` : ''
        },
        positionStyle() {
            return {
                top: `${this.verticalOffset}px`,
            }
        },
    },
    watch: {
        closed(newVal) {
            if (newVal) {
                this.visible = false
            }
        },
    },
    methods: {
        handleAfterLeave() {
            this.$destroy(true)
            this.$el.parentNode.removeChild(this.$el)
        },
        close() {
            this.closed = true
            if (typeof this.onClose === 'function') {
                this.onClose(this)
            }
        },
        clearTimer() {
            clearTimeout(this.timer)
        },
        startTimer() {
            if (this.duration > 0) {
                this.timer = setTimeout(() => {
                    if (!this.closed) {
                        this.close()
                    }
                }, this.duration)
            }
        },
        keydown(e) {
            if (e.keyCode === 27) {
                // esc关闭消息
                if (!this.closed) {
                    this.close()
                }
            }
        },
    },
    mounted() {
        this.startTimer()
        document.addEventListener('keydown', this.keydown)
    },
    beforeDestroy() {
        document.removeEventListener('keydown', this.keydown)
    },
}
</script>
